import { getSearchMovies } from "api";
import { useEffect, useState } from "react";
import { Link, useLocation, useSearchParams } from "react-router-dom";
import {
  FilterDiv, FilterInput, SvgInput, ButtonInput, FormInput, CollectionDiv,CardsDiv
} from "components/filmsgallery.styled";

export default function Home() {
  const [searchParams, setSearchParams] = useSearchParams();
  const [filmId, setFilmId] = useState(searchParams.get("filmId") || "");
  const [films, setFilms] = useState([]);

  const location = useLocation();

  const updateQueryString = async (evt) => {
    evt.preventDefault();
    const newSearchParams = new URLSearchParams(searchParams);

    if (filmId === "") {
      newSearchParams.delete("filmId");
    } else {
      newSearchParams.set("filmId", filmId);
    }

    setSearchParams(newSearchParams);

    try {
      const data = await getSearchMovies(newSearchParams.get("filmId"));
      setFilms(data.results); // Store only the 'results' array
    } catch (error) {
      console.error("Error:", error);
    }
  };

  useEffect(() => {
    fetchMovieSearch(searchParams.get("filmId"));
  }, [searchParams]);

  const fetchMovieSearch = async (params) => {
    try {
      const data = await getSearchMovies(params);
      setFilms(data.results); // Store only the 'results' array
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <FilterDiv>
      <FormInput onSubmit={updateQueryString}>
        <FilterInput
          value={filmId}
          onChange={(evt) => setFilmId(evt.target.value)}
          type="text"
        />
         <ButtonInput type="submit">
      <SvgInput xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 32 32">
      <title>search</title>
      <path d="M31.008 27.231l-7.58-6.447c-0.784-0.705-1.622-1.029-2.299-0.998 1.789-2.096 2.87-4.815 2.87-7.787 0-6.627-5.373-12-12-12s-12 5.373-12 12 5.373 12 12 12c2.972 0 5.691-1.081 7.787-2.87-0.031 0.677 0.293 1.515 0.998 2.299l6.447 7.58c1.104 1.226 2.907 1.33 4.007 0.23s0.997-2.903-0.23-4.007zM12 20c-4.418 0-8-3.582-8-8s3.582-8 8-8 8 3.582 8 8-3.582 8-8 8z"></path>
    </SvgInput>
    </ButtonInput>
      </FormInput>
      {/* Render the films */}
      <CollectionDiv>
      {films.map((film) => (
        <CardsDiv key={film.id}>
  <Link className="collection_link" state={{ from: location }} to={`/movie/${film.id}`}>
    {film.title}
  </Link>
  {film.backdrop_path ? (
            <div>
        <Link  state={{ from: location }} to={`/movie/${film.id}`}>
      <img className="cards_collection"
        src={`https://image.tmdb.org/t/p/w500${film.backdrop_path}`}
        alt=""
          />
       </Link>
    </div>
  ) : (
              <div>
                <Link  state={{ from: location }} to={`/movie/${film.id}`}>
          <img
         className="cards_collection"
        src="data:image/jpeg;base64,"
         alt=""
        width="500" 
         height="281"         
           />
               </Link>  
    </div>
  )}
</CardsDiv>
        
      ))}
    </CollectionDiv>
    </FilterDiv>
  );
}
